.member {
  text-align: center;
  margin-top: 5rem;
  position: relative;
  top:0px;
  transition: top 0.2s linear;
  
}
.member:hover{
  top: -5px;
  opacity: 0.90;
}
.member img {
  width: 25rem;
  height: 25rem;
  border-radius: 50%;
  display: block;
  margin: 0 auto;
  border: 5px solid #67e9e6;
  object-fit: cover;
}
.members-link {
  text-align: center;
}
.members-link a {
  margin: 0 7px;
  cursor: pointer;
}
.joinT {
  text-align: center;
}
.joinT h3 {
  display: inline-block;
  font-weight: 500;
  margin-right: 40px;
}
@media (max-width: 570px) {
  .member {
    margin-top: 4rem;
  }
  .member img {
    width: 25rem;
    height: 25rem;
  }
  .joinT h3 {
    margin-right: 0px;
  }
}

.media {
  color: white;
}
.single {
  display: inline-block;
  margin-left: 25px;
  margin-right: 25px;
}
.single a {
  color: rgba(231, 228, 228);
  /* position: relative; */
  /* top: 0px; */
  transition: top 0.2s linear;
}
.single a:hover {
  color: white;
  top: -5px;
}
@media (max-width: 570px) {
  .single {
    display: inline-block;
    margin-top: 50px;
